/**
 * 水球图 wataerbubble
 * @author fiona23 (fiona_fanmy@163.com)
 */

(function ($) {
  $.fn.waterbubble = function (options) {
    var config = $.extend({
      radius: 100,
      lineWidth: 5,
      data: 0.0,
      waterColor: 'rgba(25, 139, 201, 1.0)',
      textColor: 'rgba(255, 255, 128, 0.8)',
      font: '',
      wave: true,
      txt: '',
      animation: true
    }, options)
    var canvas = this[0]
    config.width = config.radius * 2
    config.height = config.radius * 2
    config.centerX = config.radius
    config.centerY = config.radius
    config.drawRadius = config.radius - config.lineWidth

    canvas.width = config.width
    canvas.height = config.height

    var parent = canvas.parentNode
    var size = config.radius * 2
    var radius = size * 3 / 4
    parent.style = 'display:inline-block;width:' + size + 'px;height:' + size + 'px;background:transparent;border:4px solid;border-color:' + config.waterColor + ';border-radius:' + radius + 'px;'
    var wb = new Waterbubble(canvas, config)

    //自定义更新值函数
    this.setValue = function (value) {
      wb.refresh(value)
    }
    //需要在调用处保存this变量，以确保setValue可以被调用
    return this
  }

  function Waterbubble (canvas, config) {
    this._init(canvas, config)
  }

  Waterbubble.prototype = {
    refresh: function (data) {
      this._buildShape(data)
    },

    _init: function (canvas, config) {
      this.config = config
      this.canvas = canvas
      this._buildShape(this.config.data)
    },

    _buildShape: function (data) {
      var ctx = this.canvas.getContext('2d')

      this.currdata = this.currdata || 0.0

      if (this.config.animation) {
        this._animate(ctx, data)
      } else {
        this._fillWater(ctx, data)
      }
    },
    _calDrawArea: function (r, data, x, y, wave) {
      var syData = r * 2 * (1 - data) + (y - r)
      var sxData = x - Math.sqrt((r) * (r) - (y - syData) * (y - syData))
      //middle co-ordinates
      var mxData = x
      var myData = syData
      //end co-ordinates
      var exData = 2 * mxData - sxData
      var eyData = syData

      var extentData //extent

      if (data > 0.9 || data < 0.1 || !wave) {
        extentData = syData
      } else {
        extentData = syData - (mxData - sxData) / 4
      }

      var startAngleData = -Math.asin((x - syData) / r)
      var endAngleData = Math.PI - startAngleData

      var quadraticData =
        {
          startX: sxData,
          startY: syData,
          cpx: (sxData + mxData) / 2,
          cpy: extentData,
          cx: mxData,
          cy: myData,
          epx: (mxData + exData) / 2,
          epy: 2 * syData - extentData,
          ex: exData,
          ey: eyData,
          startAngle: startAngleData,
          endAngle: endAngleData
        }
      return {quadraticData: quadraticData}
    },
    _fillWater: function (ctx, data) {
      ctx.clearRect(0, 0, this.config.width, this.config.height)
      ctx.beginPath()

      var area = this._calDrawArea(this.config.drawRadius, data, this.config.centerX, this.config.centerY, this.config.wave)
      ctx.beginPath()
      ctx.moveTo(area.quadraticData.startX, area.quadraticData.startY)
      ctx.quadraticCurveTo(area.quadraticData.cpx, area.quadraticData.cpy, area.quadraticData.cx, area.quadraticData.cy)
      ctx.quadraticCurveTo(area.quadraticData.epx, area.quadraticData.epy, area.quadraticData.ex, area.quadraticData.ey)

      ctx.arc(this.config.centerX, this.config.centerY, this.config.drawRadius, area.quadraticData.startAngle, area.quadraticData.endAngle, false)
      ctx.fillStyle = this.config.waterColor
      ctx.fill()

      this.currdata = this.nextdata
    },

    _drawText: function (ctx, data) {
      ctx.globalCompositeOperation = 'source-over'
      ctx.beginPath()
      var size = this.config.font ? this.config.font.replace(/\D+/g, '') : 0.4 * this.config.drawRadius
      ctx.font = this.config.font ? this.config.font : 'bold ' + size + 'px Microsoft Yahei'

      var txt = this.config.txt.length ? this.config.txt : data + '%'

      var sy = this.config.centerY + size / 2
      var sx = this.config.centerX - ctx.measureText(txt).width / 2

      ctx.fillStyle = this.config.textColor
      ctx.fillText(txt, sx, sy)
    },

    _animate: function (ctx, data) {
      this.nextdata = this.nextdata || 0.0
      var requestAnimationFrame = window.requestAnimationFrame || window.msRequestAnimationFrame || window.mozRequestAnimationFrame || window.webkitRequestAnimationFrame || function (func) {
        setTimeout(func, 16)
      }
      var self = this
      var update = function () {
        if (Math.abs(self.currdata - data) > 0.01) {
          self.nextdata = self.currdata + (data - self.currdata) / 15
          self.drawdata = self.nextdata
          self._runing = true
        } else {
          self._runing = false
          self.drawdata = data
        }
      }
      var step = function () {
        update()
        self._fillWater(ctx, self.nextdata)
        self._drawText(ctx, (self.drawdata * 100).toString().split('.')[0])
        if (self._runing) {
          requestAnimationFrame(step)
          // setTimeout(step, 100)
        }
      }
      step()
    }
  }
}(jQuery))



